import React, { Component } from 'react';
import {
    Card, CardImg, CardText, CardBody, CardImgOverlay, CardTitle, Breadcrumb, BreadcrumbItem, Button, Row, Col, Modal, ModalHeader, ModalBody,
    Form, FormGroup, Input, Label
} from 'reactstrap'
import { Control, LocalForm, Errors } from 'react-redux-form';
import { Link } from 'react-router-dom';
import dateFormat from 'dateformat';
import { Loading } from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => val && (val.length >= len);
const maxVal = (max) => (val) => !(val) || (val <= max);
const minVal = (min) => (val) => !(val) || (val >= min);
const isNumber = (val) => !isNaN(Number(val));
const validEmail = (val) => /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);
class CommentForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            firstname: '',
            yourname: '',
            rate: '',
            comment: '',
            agree: false,
            contactType: 'Tel.',
            message: '',
            isModalOpen: false,
        }
        this.toggleModal = this.toggleModal.bind(this);
    }
    toggleModal() {
        this.setState({
            isModalOpen: !this.state.isModalOpen
        });
    }
    handleInputChange(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(values) {
        this.toggleModal();
        this.props.postComment(this.props.dishId, values.rating, values.author, values.comment);
    }
    render() {
        return (
            <div>
                <Button outline onClick={this.toggleModal} ><span className="fa fa-pencil fa-lg"></span> Submit Comment</Button>
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal}>Submit Comment</ModalHeader>
                    <ModalBody>

                        <LocalForm onSubmit={(values) => this.handleSubmit(values)}>

                            <Row className="form-group">
                                <Label htmlFor="rate" md={2}>Rating</Label>
                                <Col md={10}>
                                    <Control.select model=".rate" id="rate" name="rate"
                                        placeholder=""
                                        className="form-control"
                                    >
                                        <option value="1">1</option>
                                        <option value="2">2</option>
                                        <option value="3">3</option>
                                        <option value="4">4</option>
                                    </Control.select>
                                    <Errors
                                        className="text-danger"
                                        model=".rate"
                                        show="touched"
                                    />
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Label htmlFor="yourName" md={2}>Your Name</Label>
                                <Col md={10}>
                                    <Control.text model=".yourname" id="yourname" name="yourname"
                                        placeholder="Your Name"
                                        className="form-control"
                                        validators={{
                                            required, minLength: minLength(3), maxLength: maxLength(15)
                                        }}
                                    />
                                    <Errors
                                        className="text-danger"
                                        model=".yourname"
                                        show="touched"
                                        messages={{
                                            required: 'Required',
                                            minLength: 'Must be greater than 2 characters',
                                            maxLength: 'Must be 15 characters or less'
                                        }}
                                    />
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Label htmlFor="comment" md={2}>Comment</Label>
                                <Col md={10}>
                                    <Control.textarea rows="5" model=".comment" id="comment" name="comment"
                                        className="form-control"
                                    />
                                    <Errors
                                        className="text-danger"
                                        model=".comment"
                                        show="touched"
                                    />
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Col md={{ size: 10, offset: 2 }}>
                                    <Button type="submit" color="primary">
                                        Submit
                                    </Button>
                                </Col>
                            </Row>
                        </LocalForm>
                    </ModalBody>
                </Modal>
            </div>
        );
    }
}
class DishDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }
    render() {
        function RenderComments({comments, postComment, dishId}) {
            if (comments != null) {
                return (
                    <div>
                        <h4>Comment</h4>
                        <div>
                            <ul className="list-unstyled">
                                {
                                    comments.map((comment) => {
                                        const date = dateFormat(comment.date, "mmm dd, yyyy");
                                        return (
                                            <div key={comment.id}>
                                                <div>{comment.comment}</div>
                                                <div>-- {comment.author}, {date}</div>
                                            </div>
                                        );
                                    })
                                }
                            </ul>
                            <CommentForm dishId={dishId} postComment={postComment} />
                        </div>
                    </div>
                );
            } else {
                return <div></div>
            }
        }
        const RenderDish = ({ dish }) => {
            const link = "/menu/" + dish.id;
            return <Link to={link}>
                <Card>
                    <CardImg top src={baseUrl + dish.image} alt={dish.name} />
                    <CardImgOverlay>
                        <CardTitle>{dish.name}</CardTitle>
                    </CardImgOverlay>
                </Card>
            </Link>
        }

        if (this.props.isLoading) {
            return (
                <div className="container">
                    <div className="row">
                        <Loading />
                    </div>
                </div>
            );
        }
        else if (this.props.errMess) {
            return (
                <div className="container">
                    <div className="row">
                        <h4>{this.props.errMess}</h4>
                    </div>
                </div>
            );
        }
        else if (this.props.dish != null) {
            const dish = this.props.dish;
            const comments = dish != null ? dish.comments : null;
            return (
                <div className="container">
                    <div className="row">
                        <Breadcrumb>
                            <BreadcrumbItem><Link to="/home">Home</Link></BreadcrumbItem>
                            <BreadcrumbItem><Link to="/menu">Menu</Link></BreadcrumbItem>
                            <BreadcrumbItem active>{this.props.dish.name}</BreadcrumbItem>
                        </Breadcrumb>
                        <div className="col-12">
                            <h3>{this.props.dish.name}</h3>
                            <hr />
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-12 col-md-5 m-1">
                            <RenderDish dish={this.props.dish} />
                        </div>
                        <div className="col-12 col-md-6 m-1">
                            <RenderComments comments={this.props.comments}
                                postComment={this.props.postComment}
                                dishId={this.props.dish.id}
                            />
                        </div>
                    </div>
                </div>
            );
        }
        return (
            <Card>
            </Card>
        )
    }
}

export default DishDetail;


